.paginationPanel {
    display: flex;
    align-items: center;
}
.paginationPanel button {
    background: white;
    display: flex;
    align-items: center;
    border: none;
    cursor: pointer;
    margin: 0 25px 0 0;
}
.pagesList {
    display: flex;
}
.pageLink {
    margin-right: 22px ;
    width: 22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(0, 0, 0, 0.65);
    font-size: 12px;
    cursor: pointer;
    border-radius: 4px;


}

.focus {
    background: #1890FF;

    color: white;
}